.projectsWrap {
  position: relative;
  display: flex;
  justify-content: space-around;
  align-content: space-around;
  flex-wrap: wrap;
  margin: auto;
  width: 75%;
  height: 100%;
  border-radius: 5px;
}

@media only screen and (max-width: 600px) {
  .projectsWrap {
    width: 95%;
  }
}
