.projectCard {
  position: relative;

  width: 450px;
  height: 800px;
  margin-top: 30px;
  background-color: rgba(255, 253, 253, 0.322);
  border-radius: 5px;
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.185);
}

.projectTitle {
  font-family: "Montserrat", sans-serif;
}

.projectPreview {
  height: 30%;
  width: 90%;
  margin: 0 auto;

  background-size: cover;
  opacity: 0.6;
  border-radius: 5px;
  box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.185);
}

.projectPreview:hover {
  cursor: pointer;
}

.viewWrap {
  display: flex;
  width: 20%;
  height: 70px;
  position: relative;
  align-content: center;
  margin: 0 auto;
  justify-content: space-evenly;
}

.liveView {
  position: relative;
  width: 60px;
}

.liveView:hover {
  fill: rgba(78, 72, 72, 0.562);
  cursor: pointer;
}

.githubLogo {
  position: relative;
  top: 22px;
  width: 75px;
}

.githubLogo:hover {
  fill: rgba(78, 72, 72, 0.562);
  cursor: pointer;
}

.projectDescription {
  position: relative;
  width: 80%;
  margin: 0 auto;
  height: 47%;
  border-radius: 5px;
}

.projectDescription > p {
  width: 95%;
  margin: 5px auto;
  text-align: justify;
  font-family: "Montserrat", sans-serif;
  text-indent: 2em;
}

.projectDescription > ul {
  position: relative;
  padding-top: 10px;
  top: 20px;
  list-style: none;
  text-align: justify;
  font-family: "Montserrat", sans-serif;
}

.projectDescription > ul > li {
  margin-top: 7px;
}

.projectDescription > ul > li > h1 {
  font-size: 17px;
  font-weight: bolder;
}
